@import url('https://fonts.googleapis.com/css2?family=Estonia&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: white;
}
body{
    width: 100%;
    height: 120vh;
    background:linear-gradient(to left,rgb(246, 178, 52),rgb(240, 52, 52));
    display: flex;
    justify-content: center;
    align-items: center;

}
#tagname{
    font-family: 'Estonia', cursive;
    font-size: 32px;
    margin: 5px;
    margin-bottom: 9px;
}
#search_bar{
    width: 450px;
    height: 65px;
    margin-right: 300px;
    /* border: 1px solid white; */
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}
button{
    border: 2px solid white;
    width: 60px;
    height: 45px;
    margin-left: 10px;
    border-radius: 10px;
    font-size: 16px;
    outline: none;
    background: red;
}
input{
    width: 300px;
    height: 50px;
    border-radius: 30px;
    color: black;
    font-size: 22px;
    text-align: center;
    background: rgb(255, 255, 255);
    outline: none;
}
input:hover{
    border: 2px solid red;
}
.con{
    min-width: 1400px;
    min-height: 1050px;
    margin-top: 2%;
    border: 3px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 20px;
    background: black;
}
.header{
    width: 1300px;
    height: 100px;
    padding: 5px;
    padding-top: 1px;
    margin-top: -2%;
    border-bottom: 1px solid white;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.Sider_bar{
    width: 200px;
    height: 700px;
    border-radius: 10px;
    border: 1px solid #ffffff;
    margin-top: -110px;
    display: flex;
    justify-content: left;
    padding: 10px;
    align-content: center;
    flex-direction: column;
    background: orange;
}
/* ul,li{
    list-style: none;
} */
li{
    font-size: 32px;
    margin-top: 30px;
    margin-left: 20px;
    width: 80px;
}
li:hover{
        border-bottom: 3px solid black;
        color:black;
        cursor: pointer;
}
.Down{
    justify-content: center;
    align-items: center;
    display: flex;
    width: 1300px;
    margin-top: 5px;
    /* border: 1px solid red; */
    height: 900px;

    
}
.middle{
    width: 1100px;
    height: 850px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
        margin-top: 40px;
        margin-left: 30px;
    border: 1px solid white;
}
span{
    font-size: 40px;
}
